/* ----------------------------------------------------------------
	Quotes & Blockquotes
-----------------------------------------------------------------*/

blockquote {
  padding: 10px 20px;
  margin: 20px 0;
  font-size: 17.5px;
  border-left: 5px solid #eee;
  footer {
    &:before{
      content: '- ';
    }
    display: block;
    font-size: 80%;
    color: #777;
  }
}

/* ----------------------------------------------------------------
	Footer
-----------------------------------------------------------------*/

#footer {
	position: relative;
	border-top: 5px solid rgba(0,0,0,0.2);
    text-align: center;
    text-transform: uppercase;
    background-color: rgb(51, 51, 51);
    color: rgb(204, 204, 204);
    
    #copyrights {
		padding: 40px 0;
		font-size: 14px;
		line-height: 1.8;
		background-color: rgba(0, 0, 0, 0.2);
		color: rgba(255, 255, 255, 0.247059);
    }

    .container {
		margin-top: 15px !important;
    }
}

.dark #copyrights, .dark .heading-block:not(.not-dark) {
	text-shadow: rgba(0, 0, 0, 0.0980392) 1px 1px 1px;
}
